.container {
  width: min(100% - 40px, 1110px);
  margin: 0 auto;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
}

html {
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  line-height: 1.6;
}

a {
  text-decoration: none;
}

body {
  background-color: #f9f9f9;
}

.header {
  background-color: #F1EFEE;
  padding-top: 49px;
  padding-bottom: 140px;
}
@media (max-width: 768px) {
  .header {
    display: flex;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 80px;
  }
}

.header-box {
  width: 100%;
}

.navbar {
  margin-bottom: 142px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  letter-spacing: 0.1em;
  text-align: left;
  padding-top: 30px;
}
@media (max-width: 768px) {
  .navbar {
    margin-bottom: 82px;
    font-size: 14px;
    padding-top: 25px;
  }
}

.navbar__list {
  display: flex;
  gap: 42px;
}
@media (max-width: 768px) {
  .navbar__list {
    flex-wrap: wrap;
    gap: 20px;
  }
}
@media (max-width: 576px) {
  .navbar__list {
    flex-direction: column;
    gap: 10px;
  }
}

.navbar__link--active {
  font-weight: 500;
  color: #FFFF00;
  line-height: 1.6;
}

.content {
  max-width: 540px;
}

.content__title {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 48px;
  color: #000000;
  line-height: 1.6;
  padding-bottom: 35px;
}
@media (max-width: 768px) {
  .content__title {
    font-size: 35px;
    padding-bottom: 30px;
  }
}

.content__description {
  display: flex;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #727272;
  line-height: 1.6;
  padding-bottom: 60px;
}
@media (max-width: 768px) {
  .content__description {
    font-size: 14px;
    padding-bottom: 50px;
  }
}

.content__link {
  width: 225px;
  background-color: #FFFF00;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  text-align: center;
  color: #ffffff;
  padding: 20px 48px 20px 48px;
  display: block;
}
@media (max-width: 768px) {
  .content__link {
    width: auto;
    font-size: 14px;
    padding: 15px 48px 15px 48px;
    display: inline-block;
  }
}

.section__content--about {
  margin: 0 auto;
  padding-top: 105px;
  padding-bottom: 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (max-width: 768px) {
  .section__content--about {
    padding-top: 55px;
    padding-bottom: 45px;
  }
}

.section__title--about {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 32px;
  text-align: center;
  color: #000000;
  margin-bottom: 28px;
}
@media (max-width: 768px) {
  .section__title--about {
    font-size: 28px;
    margin-bottom: 20px;
  }
}

.section__description--about {
  max-width: 524px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  text-align: center;
  color: #727272;
}
@media (max-width: 768px) {
  .section__description--about {
    font-size: 14px;
  }
}

.statistic {
  width: 100%;
  background-color: #FFFF00;
}
.statistic__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 42px;
  align-items: center;
  margin: 0 auto;
  padding-top: 93px;
  padding-bottom: 92px;
}
@media (max-width: 1150px) {
  .statistic__list {
    justify-content: center;
  }
}
@media (max-width: 768px) {
  .statistic__list {
    padding-top: 45px;
    padding-bottom: 45px;
  }
}
.statistic__item {
  width: 141px;
  display: flex;
  gap: 20px;
  align-items: center;
  margin-left: 6px;
}
.statistic__image {
  width: 50px;
  height: 50px;
}
.statistic__content {
  width: 71px;
  margin-top: 3px;
  margin-bottom: 3px;
}
.statistic__number {
  font-family: "Roboto", sans-serif;
  color: #ffffff;
  font-weight: 500;
  font-size: 21px;
  margin-left: 5px;
}
.statistic__text {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  color: #ffffff;
  margin-left: 5px;
}

.section__content--process {
  margin: 0 auto;
  padding-bottom: 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (max-width: 768px) {
  .section__content--process {
    padding-top: 55px;
    padding-bottom: 45px;
  }
}
.section__content--process .section__title--process {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 32px;
  text-align: center;
  color: #000000;
  margin-top: 105px;
  margin-bottom: 28px;
}
@media (max-width: 768px) {
  .section__content--process .section__title--process {
    font-size: 28px;
    margin-bottom: 20px;
    margin-top: 0;
  }
}
.section__content--process .section__description--process {
  max-width: 524px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  text-align: center;
  color: #727272;
}
@media (max-width: 768px) {
  .section__content--process .section__description--process {
    font-size: 14px;
  }
}

.video {
  border: 0;
  width: 100%;
  display: block;
  padding-bottom: 100px;
}
@media (max-width: 576px) {
  .video {
    padding-bottom: 50px;
  }
}
.video__iframe {
  border: 0;
  width: 100%;
  height: 600px;
  display: block;
}
@media (min-width: 992px) {
  .video__iframe {
    position: relative;
    width: 100%;
  }
}
@media (max-width: 768px) {
  .video__iframe {
    height: 300px;
  }
}

.footer {
  display: flex;
  background-color: #FFFF00;
  color: #ffffff;
  padding-top: 50px;
  padding-bottom: 50px;
}
@media (max-width: 768px) {
  .footer {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
.footer__name {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 21px;
  color: #ffffff;
  margin-bottom: 20px;
}
@media (max-width: 768px) {
  .footer__name {
    font-size: 18px;
    margin-bottom: 15px;
  }
}
.footer__copyright {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 1.6;
  color: #ffffff;
}
@media (max-width: 768px) {
  .footer__copyright {
    font-size: 12px;
  }
}