@use 'mixins' as *;
@use 'abstracts' as *;

.section__content--process {
  margin: 0 auto;
  padding-bottom: 90px;
  @include flex(column, center, center);
  @include respond-to(md) {
    padding-top: 55px;
    padding-bottom: 45px;
  }

  .section__title--process {
    font-family: $font-family;
    font-weight: 500;
    font-size: 32px;
    text-align: center;
    color: $color-black;
    margin-top: 105px;
    margin-bottom: 28px;
    @include respond-to(md) {
      font-size: 28px;
      margin-bottom: 20px;
      margin-top: 0;
    }
  }

  .section__description--process {
    max-width: 524px;
    font-family: $font-family;
    font-weight: 400;
    font-size: 16px;
    line-height: $line-height;
    text-align: center;
    color: $color-content;
    @include respond-to(md) {
      font-size: 14px;
    }
  }
}
